import React, { Component } from 'react'
import Cell from './common/Cell';
import RenderLine from './renderLine';
import { motion } from 'framer-motion';
// import Status from './status';

export class Game extends Component {
    state = {
        grid : [
            {id : 0, statues : false, value : null, div: null},
            {id : 1, statues : false, value : null, div: null},
            {id : 2, statues : false, value : null, div: null},
            {id : 3, statues : false, value : null, div: null},
            {id : 4, statues : false, value : null, div: null},
            {id : 5, statues : false, value : null, div: null},
            {id : 6, statues : false, value : null, div: null},
            {id : 7, statues : false, value : null, div: null},
            {id : 8, statues : false, value : null, div: null}
        ],
        flip : true,
        win : false,
        one : true,
        cls : null,
        restart : null
    }
    handleClick = (cell) => {
        const O = <motion.h1 initial={{scale:3}} animate={{scale:1}}>O</motion.h1>
        const X = <motion.h1 initial={{scale:3}} animate={{scale:1}} >X</motion.h1>
        const { grid, flip } = this.state;
        console.log(`cell`, cell)
        const data = grid;
        
        for (let i in data) {
            if(data[i] === data[cell]){
                flip ? data[i].div = X : data[i].div = O ;
                flip ? data[i].value = "X" : data[i].value = "O" ;
                this.setState({flip : !flip})
                data[cell].statues = !data[cell].statues;
                break ;
            } 
        }
        this.setState({ grid : data })
        
        this.checkWin();
        
    }

    checkWin = () => {
        const { grid } = this.state;

        // this.cheackTie();

        
        this.cheack(grid[0], grid[1], grid[2]);
        this.cheack(grid[3], grid[4], grid[5]);
        this.cheack(grid[6], grid[7], grid[8]);
        this.cheack(grid[0], grid[3], grid[6]);
        this.cheack(grid[1], grid[4], grid[7]);
        this.cheack(grid[2], grid[5], grid[8]);
        this.cheack(grid[0], grid[4], grid[8]);
        this.cheack(grid[2], grid[4], grid[6]);
    }
    cheack = (one, two, three) => {
        
        if( one.value === two.value && two.value === three.value && three.value && this.state.one ){ 
            let one1 = true;
            this.setState({ one : false })
            if ( one1 ){
                one1 = false;
                this.setState({win : true})
                console.log(`win :`, this.state.win)
                this.setState({cls : `line line${one.id}${two.id}${three.id}`})
                console.log(`line line${one.id}${two.id}${three.id}`)
            }
        }
    }
    // cheackTie = () => {
    //     const { grid } = this.state;
    //     const data = grid;
    //     let j = 0;
    //     for (let i in data) {
    //         j++;
    //         if(data[i].statues ){
    //             console.log("Tie")
    //             break ;
    //         } else {
    //             console.log("not yet")
    //         }
    //     }
    // }
    handleRestart = () => {
        document.getElementById('Sub-line').style.zIndex = "-1";
        const flip =  true
        const win = false
        const one = true
        const cls = null

        const re =[
            {id : 0, statues : false, value : null, div: null},
            {id : 1, statues : false, value : null, div: null},
            {id : 2, statues : false, value : null, div: null},
            {id : 3, statues : false, value : null, div: null},
            {id : 4, statues : false, value : null, div: null},
            {id : 5, statues : false, value : null, div: null},
            {id : 6, statues : false, value : null, div: null},
            {id : 7, statues : false, value : null, div: null},
            {id : 8, statues : false, value : null, div: null}
        ]
        this.setState({grid : re})
        this.setState({flip})
        this.setState({win})
        this.setState({one})
        this.setState({cls})

        console.log(this.state.grid)
    }
    componentDidMount = () => {
        
    }
    
    render() {
        return (
            <div className="All">
                <div className="status colum"></div>
                <div className="left player"></div>
                <div className="box row align-items-center p-10">
                {
                        this.state.grid.map(item => 
                        <Cell 
                            key={item.id} 
                            id={item.id} 
                            value={item.value} 
                            div={item.div}
                            statues={item.statues}
                            onClick={this.handleClick} />)
                }
                <div className="sub-line" id="Sub-line">
                    <div className="line-contain"></div>
                    <RenderLine  disabled={this.state.win} cls={this.state.cls} />
                </div>
                </div>
                <div className="right player"></div>
                <motion.button onClick={this.handleRestart}  className="restart colum">restart</motion.button>
            </div>
        )
    }
}

export default Game

