import React, { Component } from 'react'
import { motion } from 'framer-motion';

export class Cell extends Component {
    render() {
        const {id, statues, onClick, div} = this.props;
        return (
            <motion.button
                whileHover={{ scale: 1.1 }}
                whileTap={{ scale: 0.9 }}
                disabled={statues} 
                onClick={ () => onClick(id)} 
                className="cell cus col-4">
                {div}
            </motion.button>
        )
    }
}

export default Cell

