import React from 'react'
import { motion } from 'framer-motion';

const renderLine = ({ disabled, cls }) => {
    
    if (disabled){ 
        document.getElementById('Sub-line').style.zIndex = "1";
        if (cls === "line line048") return ( <motion.div initial={{scale:2}} animate={{scale:1, rotate:-45, y:-150 }} className={cls}></motion.div> )
        if (cls === "line line246") return ( <motion.div initial={{scale:2}} animate={{scale:1, rotate:45, y:-150 }} className={cls}></motion.div> )
        return ( <motion.div initial={{scale:2}} animate={{scale:1 }} className={cls}></motion.div> )
    }
    return null
}

export default renderLine
